.metrics {
  .levelMenu {
    background: #fafaf8;
    .numbers {
      font-size: 1.8em;
      font-weight: bold;
      text-align: center;
      span {
        display: block;
        font-weight: normal;
        font-size: .5em;
      }
    }
  }
  li {
    &:before {
      content: '\00bb';
      display: inline-block;
      color: $Gray;
      width: 13px;
      margin-left: -13px;
    }
    a {
      font-weight: bold;
      font-size: .9em;
    }
  }
}

.periodicals {
  .levelMenu {
    background: #fafaf8;
    padding: 10px 15px;

    >ul{
      >li{
        display: inline-block;
        text-align: center;
        background: url(../img/dashline.v.png) top right repeat-y;
        @include border-box;

        &:last-child {
          background: none;
        }

        >a{
          padding: 0 12px;
          font-size: 1.1em;
          font-weight: bold;
          line-height: 58px;
        }
      }
    }
  }
  .search {
    float: right;
  }
}

.slide-item {
  &.release {
    display: inline-block;
    vertical-align: top;
    width: 160px;
    padding: 0 10px 0 0;
